import React, { useState } from "react";
import QRCode from "qrcode";
import Downloadbutton from "./downloadbutton";
// console.log(Downloadbutton);
function Greenbox() {
  const [userInput, setUserInput] = useState("");
  const [flag, setFlag] = useState(false);
  const [mouseFlag, setMouseflag] = useState(false);
  const [imgurl, setimgurl] = useState("");
  function handleChange(event) {
    setUserInput(event.target.value);
    setFlag(false);
  }
  function handleClick() {
    setFlag(true);
    console.log(userInput);
    QRCode.toDataURL(userInput, function (err, url) {
      if (err) {
        setimgurl("abc.png");
      }
      setimgurl(url);
    });
  }
  function handleMouseOver() {
    setMouseflag(true);
  }
  function handleMouseOut() {
    setMouseflag(false);
  }
  return (
    <div>
      <div id="greenbox">
        <h1>Welcome to QR-Code Generator </h1>
        <input
          className="input"
          onChange={handleChange}
          placeholder="Type URL"
        ></input>
        <button
          id="button"
          onClick={handleClick}
          onMouseOver={handleMouseOver}
          onMouseOut={handleMouseOut}
          style={{ background: mouseFlag ? "black" : "white" }}
          type="submit"
        >
          {mouseFlag ? console.log("over") : console.log("out")}
          Generate
        </button>
      </div>
      <div>
        {flag ? (
          <div id="image">
            {/* <h2 id="imageheading">Qr code generated</h2> */}
            {/*eslint-disable-next-line*/}
            <img
              src={imgurl}
              alt="Image could not be loaded"
              height="300px"
            ></img>
            {/* <button id="download">download</button> */}
            {typeof imgurl !== "undefined" ? (
              <Downloadbutton url={imgurl} />
            ) : null}
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
}
export default Greenbox;
